.hide{
  background: rgb(249, 249, 250);
  height: 100%;
  width: 15%;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
 i{
  cursor: pointer;
}

.info{
  height: 100%;
  width: 85%;
  background: rgb(249, 249, 250);
  padding: 5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.email {
  height: 5%;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: end;
      align-items: flex-end;
  font-size: 0.8em;
  padding: 4px;
  opacity: .9;
}

.container {
  height: 100vh;
}

.title {
  text-align: center;
  z-index: 1000;
  
  text-transform: uppercase;
  border-bottom: 1px solid black;
}

.title h1 {
  font-size: 5em;
  margin-top: 0;
  margin-bottom: 10px;
  z-index: inherit;
}

.title h3 {
  font-size: 2em;
  margin-bottom: 10px;
}

.icon-wrap {
  display: -ms-flexbox;
  display: flex;
  padding: 20px;
}

.icon {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  margin: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(255, 255, 255);
  background-color: black;
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 700;
  -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  cursor: pointer;
}



.square-wrap {
  height: 300px;
  width: 300px;
  position: absolute;
  top: 0;
  right: 0;
}

.square-wrap img {
  height: 100%;
  width: auto;
}

.icon-second {
  
  height: 45px;
  width: 45px;
  border-radius: 50%;
  margin: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(255, 255, 255);
  background-color: rgb(19, 18, 18);
  -ms-flex-pack: center;
      justify-content: center;
  font-weight: 300;
  font-size:.5rem;
  -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
  cursor: pointer;
}
@media screen and (max-width: 968px) {

  .title h1 {
    font-size: 3rem;
    margin-top: 0;
    margin-bottom: 10px;
  }
  
  .title h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }
  .icon-wrap {
    display: -ms-flexbox;
    display: flex;
    z-index: 500;
  
  }

  .icon {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: rgb(255, 255, 255);
    background-color: black;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 700;
    -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    cursor: pointer;
  }


  .square-wrap {
    height: 150px;
    width: 150px;
    position: absolute;
    top: 0;
    right: 0;
  }


}
.return-wrap{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40%;
  font-size:14px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 20px;
 text-transform: uppercase;
  
}
.Return p{
  margin-left: 9px;
}
.Return{
  
  height: 30%;
  font-size:14px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 20px;
 text-transform: uppercase;
  
}

@media screen and (max-width: 468px) {

  .title h1 {
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 10px;
   
  }
  
  .title h3 {
    font-size: 1rem;
    margin-bottom: 10px;
    
  }
  .icon-wrap {
    display: -ms-flexbox;
    display: flex;
  
  }

  .icon {
    -ms-flex: 1 1;
        flex: 1 1;
    height: 60px;
    width: 60px;
    border-radius: 50%;
    margin: 2px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: rgb(255, 255, 255);
    background-color: black;
    -ms-flex-pack: center;
        justify-content: center;
    font-weight: 700;
    -webkit-box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    box-shadow: 0px 4px 24px -4px rgba(0, 0, 0, 0.65);
    cursor: pointer;
  }


  .square-wrap {
    height: 150px;
    width: 150px;
    position: absolute;
    top: 0;
    right: 0;
  }

  @media only screen and (orientation: landscape) {
 
    .square-wrap {
      height: 50px;
      width: 50px;
      position: absolute;
      top: 0;
      right: 0;
    }
    .icon {
      -ms-flex: 1 1;
          flex: 1 1;
      height:50px;
      width: 50px;
      border-radius: 50%;
    }
    .Return{
      max-height: 100%;
      font-size:14px;
      display:-ms-flexbox;
      display:flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
      padding: 20px;
     
     text-transform: uppercase;
      
    }
    .icon-wrap{
      padding: 0 0 150px 0;
    }
  
  }


}




